<template>
	<div class="active-code-wrapper">
		<div class="header">
			<div class="title">产品列表</div>
			<div style="height: 44px;"></div>
		</div>
		<div class="split-line"></div>
		<div class="main-content">
			<div class="search-area">
				<div class="input-area">
					<div class="input-item">
						<span>申请时间：</span>
						<el-date-picker
							v-model="searchForm.startTime"
							type="date"
							placeholder="选择日期"
						>
						</el-date-picker>
						<span style="margin: 0 10px;">至</span>
						<el-date-picker
							v-model="searchForm.finishTime"
							type="date"
							placeholder="选择日期"
						>
						</el-date-picker>
					</div>
					<div class="input-item">
						<span>产品SN：</span>
						<el-input v-model="searchForm.sn" placeholder="请输入"></el-input>
					</div>
					<div class="input-item">
						<span>产品：</span>
						<el-input v-model="searchForm.productName" placeholder="请输入"></el-input>
					</div>
				</div>
				<div class="search-btn" @click="searchActiveCode">
					查询
				</div>
			</div>
			<div class="table-content">
				<el-table
					ref="multipleTable"
					:data="tableData"
					tooltip-effect="dark"
					style="width: 100%"
					:stripe="true"
				>
					<el-table-column label="序号" width="120" align="center">
						<template slot-scope="scope">{{ (scope.$index + 1) + (searchForm.page - 1) * 10    }}</template>
					</el-table-column>
					<el-table-column prop="productName" label="产品" width="250"> </el-table-column>
					<el-table-column prop="sn" label="产品SN" width="200"> </el-table-column>
					<el-table-column prop="activationCodeNo" label="激活码"> </el-table-column>
					<el-table-column prop="activationTime" label="申请时间" width="200">
					</el-table-column>
				</el-table>
			</div>
			<div class="page-area">
				<el-pagination
					:current-page.sync="searchForm.page"
					background
					layout="->,prev, pager, next"
					prev-text="上一页"
					next-text="下一页"
					:total="total"
					@current-change="getCodeByPage"
				>
				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
import { getCodeList } from "@/api/code"
import { formatTime } from "@/utils/index";
export default {
	data() {
		return {
			start_time: "",
			current_page: 1,
			product_name: "",
			sn_code: "",
			options: [],
			sn_loading: false,
			tableData: [],
			total: 0,
			searchForm: {
				startTime: "",
				finishTime: "",
				productName: "",
				size: 10,
				page: 1,
				sn: "",
			},
		};
	},
	created() {},
	mounted() {
		this.$store.commit("setRouteName", {
			name: "激活码管理",
      		path: "/activeCodeManage"
		})
		getCodeList(this.searchForm).then(res => {
			this.total = res.data.data.count;
			this.tableData = res.data.data.rowList;
		})
	},
	computed: {},
	methods: {
		requestSN(value) {
			this.sn_loading = true;
			setTimeout(() => {
				this.sn_loading = false;
			}, 3000);
			console.log(value);
		},
		searchActiveCode() {
			let data = this.searchForm;
			data.page = 1;
			if (data.startTime) {
				data.startTime = formatTime(data.startTime, 10);
			}
			if (data.finishTime) {
				data.finishTime = formatTime(data.finishTime, 10);
			}
			getCodeList(data).then((res) => {
				this.total = res.data.data.count;
				this.tableData = res.data.data.rowList;
			});
		},
		getCodeByPage(page) {
			let data = this.searchForm;
			data.page = page;
			if (data.startTime) {
				data.startTime = formatTime(data.startTime, 10);
			}
			if (data.finishTime) {
				data.finishTime = formatTime(data.finishTime, 10);
			}
			getCodeList(data).then((res) => {
				this.total = res.data.data.count;
				this.tableData = res.data.data.rowList;
			});
		}
	},
};
</script>

<style lang="scss" scoped>
.active-code-wrapper {
	.header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		.title {
			font-size: 24px;
			color: #313552;
			font-weight: bold;
		}
	}
	.split-line {
		height: 1px;
		background-color: #d9d9d9;
		margin: 36px 0 32px 0;
	}
	.main-content {
		.search-area {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 28px;
			.input-area {
				display: flex;
				align-items: center;
				justify-content: flex-start;
				.el-input,
				.el-select {
					width: 220px;
				}
				.input-item {
					margin-right: 25px;
					span {
						margin-right: 15px;
						font-size: 20px;
						color: #8c8c8c;
					}
				}
				.input-item:last-child {
					margin-right: 0px;
				}
			}
			.search-btn {
				width: 120px;
				height: 44px;
				line-height: 44px;
				font-size: 18px;
				color: #fff;
				background-color: #425acb;
				border-radius: 5px;
				text-align: center;
				cursor: pointer;
			}
		}
		.table-content {
		}
		.page-area {
			margin-top: 33px;
		}
	}
}
</style>
